.dreamview-popover {
  font-family: PingFangSC-Medium;
  font-size: 16px;
  font-weight: 500;
}
.dreamview-popover .dreamview-popover-inner {
  background-color: #282B36;
  border-radius: 6px;
  padding: 5px 10px;
}
.dreamview-popover .dreamview-popover-title {
  color: #fff;
}
.dreamview-popover .dreamview-popover-inner-content {
  color: #fff;
}
.dreamview-popover .dreamview-popover-arrow::before,
.dreamview-popover .dreamview-popover-arrow::after {
  background-color: #282B36;
}
